<template>
  <div class="registered">
    <div class="info"></div>
    <div class="options ali-c">
      <img src="@/assets/a_username@2x.png" alt />
      <input type="text" maxlength="11" placeholder="" v-model="account" />
    </div>
    <div class="options ali-c">
      <img src="@/assets/a_password@2x.png" alt />
      <input type="password" maxlength="20" placeholder="" v-model="password" />
    </div>
    <div class="options ali-c">
      <img src="@/assets/tel.png" alt />
      <input type="tel" maxlength="11" placeholder="" v-model="tel" />
    </div>
    <div class="options ali-c">
      <img src="@/assets/a_unitname@2x.png" alt />
      <input type="text" maxlength="20" v-model="companyName" placeholder="" />
    </div>
    <div class="options ali-c">
      <img src="@/assets/a_fullname@2x.png" alt />
      <input type="text" maxlength="4" placeholder="" v-model="userName" />
    </div>
    <div class="options ali-c" @click="sexSelect">
      <img src="@/assets/a_gender@2x.png" alt />
      <input type="text" placeholder="" readonly v-model="sex" />
    </div>
    <div class="options ali-c" @click="timeSelect">
      <img src="@/assets/a_years@2x.png" alt />
      <input type="text" placeholder="" readonly v-model="birthday" />
    </div>
    <div class="options ali-c">
      <van-uploader :after-read="afterRead" :max-count="1">
        <img src="@/assets/a_picture@2x.png" alt />
        <input type="text" placeholder="" v-if="!image" readonly />
        <img :src="'http://sggsxt.0791jr.com' + image" class="up_img" v-if="image" alt />
      </van-uploader>
    </div>
    <van-button class="logIn_btn" round size="large" @click="toReg" type="info"></van-button>
    <div class="text ali-c jus-c">
      <p>
        ?
        <router-link to="/index/login"  tag="span"></router-link>
      </p>
    </div>

    <!--  -->
    <van-popup v-model="sexShow" position="bottom" :style="{ height: '40%' }">
      <van-picker show-toolbar :columns="sexArr" @confirm="sexOnConfirm" @cancel="sexOnCancel" />
    </van-popup>
    <!--  -->
    <van-popup v-model="timeShow" position="bottom" :style="{ height: '40%' }">
      <van-datetime-picker
        v-model="currentDate"
        type="date"
        title=""
        :min-date="minDate"
        :max-date="maxDate"
        :formatter="formatter"
        @confirm="timeConfirm"
        @cancel="timeCancel"
      />
    </van-popup>
  </div>
</template>

<script>
import Vue from "vue";
import { Toast, Uploader } from "vant";
import Exif from "exif-js";
import { register, setImg } from "@/request/api.js";
import { u_formatTimestamp, u_Reg } from "@/utils/utils.js";

Vue.use(Toast, Uploader);
export default {
  name: "",
  components: {
    "van-uploader": Uploader,
  },
  data() {
    return {
      //---------------------------------------
      account: "", //
      password: "", //
      companyName: "", //
      userName: "", //
      sex: "", // 1 2
      birthday: "", //
      image: "", //
      tel: "", // 

      //---------------------------------------
      sexShow: false, //
      sexArr: ["", ""], //
      timeShow: false,
      minDate: new Date(1960, 0, 1),
      maxDate: new Date(),
      currentDate: new Date(),

      picValue: "", // 
    };
  },
  watch: {},
  computed: {},
  methods: {
    //---------------------------------
    sexSelect() {
      this.sexShow = true;
    },
    sexOnConfirm(e) {
      this.sex = e;
      this.sexShow = false;
    },
    sexOnCancel(e) {
      this.sexShow = false;
    },
    //--------------------------------
    timeSelect() {
      this.timeShow = true;
    },
    timeConfirm(e) {
      this.timeShow = false;

      let time = u_formatTimestamp(e, {
        type: "ymd",
      });

      this.birthday = time;
    },
    timeCancel() {
      this.timeShow = false;
    },
    formatter(type, val) {
      if (type === "year") {
        return `${val}`;
      } else if (type === "month") {
        return `${val}`;
      }
      return val;
    },
    //--------------------------------
    afterRead(e) {
      console.log(e);
      this.picValue = e.file;
      this.imgPreview(this.picValue);
    },
    imgPreview(file) {
      let self = this;
      let Orientation;
      //
      Exif.getData(file, function () {
        Orientation = Exif.getTag(this, "Orientation");
      });
      // FileReader
      if (!file || !window.FileReader) {
        Toast.clear();
        this.$tip("FileReader!");
        return;
      }

      if (/^image/.test(file.type)) {
        // reader
        let reader = new FileReader();
        // 2 base64 
        reader.readAsDataURL(file);
        // 
        reader.onloadend = function () {
          let result = this.result;
          let img = new Image();
          img.src = result;
          //100K,
          if (this.result.length <= 100 * 1024) {
            self.postImg(this.result);
          } else {
            img.onload = function () {
              let data = self.compress(img, Orientation);
              self.postImg(data);
            };
          }
        };
      }
    },
    postImg(imgs) {
      //
      let uploadfile = this.dataURLtoFile(imgs);
      uploadfile = new File([uploadfile], this.picValue.name, {
        type: this.picValue.type,
      });

      let param = new FormData(); //form
      param.append("image", uploadfile); //appendform
      this.$loading()
      setImg(param).then((res) => {
        Toast.clear()
        if (res.code == 1) {
          this.image = res.data.img;
        } else {
          console.log(res);
          this.$tip(res.msg);
        }
      });

      
    },
    rotateImg(img, direction, canvas) {
      //4
      const min_step = 0;
      const max_step = 3;
      if (img == null) return;
      //imgimg
      let height = img.height;
      let width = img.width;
      let step = 2;
      if (step == null) {
        step = min_step;
      }
      if (direction == "right") {
        step++;
        //
        step > max_step && (step = min_step);
      } else {
        step--;
        step < min_step && (step = max_step);
      }
      //
      let degree = (step * 90 * Math.PI) / 180;
      let ctx = canvas.getContext("2d");
      switch (step) {
        case 0:
          canvas.width = width;
          canvas.height = height;
          ctx.drawImage(img, 0, 0);
          break;
        case 1:
          canvas.width = height;
          canvas.height = width;
          ctx.rotate(degree);
          ctx.drawImage(img, 0, -height);
          break;
        case 2:
          canvas.width = width;
          canvas.height = height;
          ctx.rotate(degree);
          ctx.drawImage(img, -width, -height);
          break;
        case 3:
          canvas.width = height;
          canvas.height = width;
          ctx.rotate(degree);
          ctx.drawImage(img, -width, 0);
          break;
      }
    },
    compress(img, Orientation) {
      let canvas = document.createElement("canvas");
      let ctx = canvas.getContext("2d");
      //canvas
      let tCanvas = document.createElement("canvas");
      let tctx = tCanvas.getContext("2d");
      let initSize = img.src.length;
      let width = img.width;
      let height = img.height;
      //400
      let ratio;
      if ((ratio = (width * height) / 4000000) > 1) {
        console.log("400");
        ratio = Math.sqrt(ratio);
        width /= ratio;
        height /= ratio;
      } else {
        ratio = 1;
      }
      canvas.width = width;
      canvas.height = height;
      //        
      ctx.fillStyle = "#fff";
      ctx.fillRect(0, 0, canvas.width, canvas.height);
      //100
      let count;
      if ((count = (width * height) / 1000000) > 1) {
        console.log("100W");
        count = ~~(Math.sqrt(count) + 1); //
        //            
        let nw = ~~(width / count);
        let nh = ~~(height / count);
        tCanvas.width = nw;
        tCanvas.height = nh;
        for (let i = 0; i < count; i++) {
          for (let j = 0; j < count; j++) {
            tctx.drawImage(
              img,
              i * nw * ratio,
              j * nh * ratio,
              nw * ratio,
              nh * ratio,
              0,
              0,
              nw,
              nh
            );
            ctx.drawImage(tCanvas, i * nw, j * nh, nw, nh);
          }
        }
      } else {
        ctx.drawImage(img, 0, 0, width, height);
      }
      //ios 
      if (Orientation != "" && Orientation != 1) {
        switch (Orientation) {
          case 6: //90
            this.rotateImg(img, "left", canvas);
            break;
          case 8: //90
            this.rotateImg(img, "right", canvas);
            break;
          case 3: //180
            this.rotateImg(img, "right", canvas); //
            this.rotateImg(img, "right", canvas);
            break;
        }
      }
      //
      let ndata = canvas.toDataURL("image/jpeg", 0.1);
      console.log("" + initSize);
      console.log("" + ndata.length);
      console.log(
        "" + ~~((100 * (initSize - ndata.length)) / initSize) + "%"
      );
      tCanvas.width = tCanvas.height = canvas.width = canvas.height = 0;
      return ndata;
    },
    dataURLtoFile(dataurl) {
      var arr = dataurl.split(","),
        mime = arr[0].match(/:(.*?);/)[1],
        bstr = atob(arr[1]),
        n = bstr.length,
        u8arr = new Uint8Array(n);
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new Blob([u8arr], { type: mime });
    },
    // 
    toReg() {
      if (!this.account) {
        this.$tip("!");
        return;
      }
      if (!this.password) {
        this.$tip("!");
        return;
      }
      if (!this.tel) {
        this.$tip("!");
        return;
      }
      if (!u_Reg(this.tel, "phone")) {
        this.$tip("!");
        return;
      }
      if (!this.companyName) {
        this.$tip("!");
        return;
      }
      if (!this.userName) {
        this.$tip("!");
        return;
      }
      if (!this.sex) {
        this.$tip("!");
        return;
      }
      if (!this.birthday) {
        this.$tip("!");
        return;
      }
      if (!this.image) {
        this.$tip("!");
        return;
      }

      this.$loading();
      register({
        username: this.account,
        password: this.password,
        imgae: this.image,
        nickname: this.userName,
        mobile: this.tel,
        gender: this.sex == "" ? 1 : 2,
        birthday: this.birthday,
        group_name: this.companyName,
      }).then((res) => {
        Toast.clear();
        if (res.code == 1) {
          this.$tip("!");
          // localStorage.setItem("userInfos", JSON.stringify(res.data.user));
          // localStorage.setItem("token", res.data.user.token);
          setTimeout(() => {
            this.$router.push("/index/login");
          }, 1500);
        } else {
          this.$tip(res.msg);
        }
      });
    },
  },
  created() {},
  mounted() {},
};
</script>

<style scoped lang="less">
.registered {
  .info {
    font-size: 0.72rem;
    font-family: PingFang SC;
    font-weight: bold;
    color: #010101;
  }
  .options {
    min-height: 1rem;
    padding: 0 0.2rem;
    margin: 0.4rem 0;
    border-bottom: 0.02rem solid #e4e4e4;
    img {
      width: 0.4rem;
      height: 0.4rem;
      margin-right: 0.2rem;
    }
    input {
      height: 0.6rem;
      padding: 0.1rem;
      flex: 1;
    }
    .up_img {
      width: 1.2rem;
      height: 1.2rem;
      margin: 0.2rem 0;
      border: 1px solid #e9e9e9;
      border-radius: 0.1rem;
    }
  }
  .text {
    margin-top: 0.3rem;
    font-size: 0.32rem;
    font-family: PingFang SC;
    color: #9a9a9a;
    span {
      color: #3e80fc;
    }
  }
}
</style>
